.mainactions {
  /*max-width: 800px;*/
  max-width: 700px;
  min-width: 250px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-size:0;
}

.download-btn {
  color:#FFFFFF;
  font-weight:bold;
  /*text-transform:uppercase;*/
  text-decoration:none;
  border-radius:6px;
  transition:0.1s ease;
  
  display: block;
  display: inline-block;
  display: inline-flex;
  margin: 0.4em; 
}

.download-btn-now {
    width: $download-btn-appstore-width;
    height: $download-btn-appstore-height;
    line-height: $download-btn-appstore-height;
    font-size:$download-btn-appstore-font-size;
    background: url(/assets/images/badges/Download_on_the_Gongyixing.svg) no-repeat center;
    background-size: $download-btn-appstore-width $download-btn-appstore-height;
    /*text-transform:uppercase;*/

}

.download-btn-now span {
    width: $download-btn-appstore-width;
    text-align: center;
    font-size: $download-btn-appstore-font-size;
}

@media screen {

  .download-btn span {
    visibility: hidden;
  }

  .download-btn-now span {
    visibility: visible;
  }

}

